<template>
    <div>
        <Form ref="loginForm" :model="loginForm" :rules="loginRules" label-position="left" :label-width="70"
        :hide-required-mark="true" :label-colon="true">
            <FormItem prop="username" label="">
                <Input type="text" v-model="loginForm.username" placeholder="" style="width: 350px" clearable>
                </Input>
            </FormItem>
            <FormItem prop="password" label="">
                <Input type="password" v-model="loginForm.password" placeholder="" style="width: 350px" password>
                </Input>
                <span class="forgetPass" @click="findPassModal=true">?</span>
            </FormItem>
            <FormItem>
                <Button type="primary" @click="login('loginForm')" style="width: 150px"></Button>
                <Button type="info" @click="addUserDrawer=true" style="width: 150px;margin-left:20px"></Button>
            </FormItem>
        </Form>

        <Drawer
                title=""
                v-model="addUserDrawer"
                width="500"
                :mask-closable="false"
        >
            <Form ref="addUserForm" :model="addUserForm" :rules="addUserRules" :label-width="80">
                <FormItem label="" prop="username">
                    <Input v-model="addUserForm.username" placeholder=""></Input>
                </FormItem>
                <FormItem label="" prop="password">
                    <Input v-model="addUserForm.password" placeholder="" type="password" password></Input>
                </FormItem>
                <FormItem label="" prop="checkpass">
                    <Input v-model="addUserForm.checkpass" placeholder="" type="password" password></Input>
                </FormItem>
                <FormItem label="" prop="realname">
                    <Input v-model="addUserForm.realname" placeholder=""></Input>
                </FormItem>
                <FormItem label="" prop="number">
                    <Input v-model="addUserForm.number" placeholder=""></Input>
                </FormItem>
                <FormItem label="" prop="email">
                    <Input v-model="addUserForm.email" placeholder=""></Input>
                </FormItem>
                <FormItem class="addUserDrawerFooter">
                <Button type="primary" @click="addUserSubmit('addUserForm')"></Button>
                <Button @click="addUserDrawer=false" style="margin-left: 8px"></Button>
                </FormItem>
            </Form>
        </Drawer>

        <Modal
                :mask-closable="false"
                v-model="findPassModal"
                title=""
                width="40%"
                :loading="loading"
                @on-ok="findPass">
            <Form ref="findPassForm" :model="findPassForm" :rules="addUserRules" :label-width="80">
                <FormItem label="" prop="email">
                    <Input v-model="findPassForm.email" placeholder=""></Input>
                </FormItem>
            <FormItem label="" prop="password">
                <Input v-model="findPassForm.password" placeholder="" type="password" password></Input>
            </FormItem>
            <FormItem label="" prop="checkpass">
                <Input v-model="findPassForm.checkpass" placeholder="" type="password" password></Input>
            </FormItem>
            </Form>
        </Modal>

        <Modal v-model="verifyModal" :closable="false" footer-hide width="350px">
            <slide-verify  ref="slideblock"
                           @again="onAgain"
                           @success="onSuccess"
                           @fail="onFail"
                           :accuracy="accuracy"
                           :imgs="imgs"
                           slider-text=""
            ></slide-verify>
        </Modal>
    </div>
</template>

<script>
    export default {
        name: "LoginForm",
        data(){
            var validatePass = (rule, value, callback) => {
                if (value !== this.addUserForm.password && value !==this.findPassForm.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            var validateRealname = (rule, value, callback) => {

                if (Number.isFinite(value)) {
                    callback(new Error('!'));
                }
                else if(value.length<2||value.length>20){
                    callback(new Error('!'));
                }
                else {
                    callback();
                }
            };
            var validateNumber = (rule, value, callback) => {
                //
                if (!(/^1[3456789]\d{9}$/.test(value))){
                    callback(new Error('!'));
                }
                else {
                    callback();
                }
            };
            return{
                loginForm: {
                    username: '',
                    password: ''
                },
                loginRules: {
                    username: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', min: 3,max:10, message: '3-10', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', min: 6,max:14, message: '6-14', trigger: 'blur' }
                    ]
                },
                addUserForm:{
                    username:'',
                    password:'',
                    checkpass:'',
                    realname:'',
                    number:'',
                    email:''
                },
                addUserRules:{
                    username: [
                        { required: true, message: '', trigger: 'blur' },
                        { min:3,max:10, message: '3-10', trigger: 'blur' },
                    ],
                    password:[
                        { required: true, message: '', trigger: 'blur' },
                        { min:6,max:14, message: '6-14', trigger: 'blur' }
                    ],
                    checkpass:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validatePass,  trigger: 'blur' }
                    ],
                    realname:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateRealname, trigger: 'blur'}
                    ],
                    number:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateNumber, trigger: 'blur' }
                    ],
                    email:[
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'email', message: '', trigger: 'blur' }
                    ]
                },
                findPassForm:{
                    email:'',
                    password:'',
                    checkpass:''
                },
                findPassModal:false,
                verifyModal:false,
                loading:true,
                addUserDrawer:false,
                // 15
                accuracy: 7,
                imgs:["http://q7u9zowxe.bkt.clouddn.com/guimie.jpg",
                    "http://q7u9zowxe.bkt.clouddn.com/wlopbackground.jpg",
                    "http://q7u9zowxe.bkt.clouddn.com/timg.jpg",
                    "http://q7u9zowxe.bkt.clouddn.com/yourname.jpg"]
            }
        },
        methods:{
            verfiySuccess(){
                this.verifyModal=false;
                let user=this.$qs.stringify(this.loginForm);
                this.axios.post('/customer/login',user)
                    .then(res=>{
                        console.log(res)
                        if(res.data.data.token!=null&&res.data.data.token!==''){
                            localStorage.setItem('token',res.data.data.token);
                            localStorage.setItem('loginUser',JSON.stringify(res.data.data.user));
                            this.axios.defaults.headers.common['token']=res.data.data.token;
                            this.$router.push('/welcome');
                            this.$Message.success('!');
                        }else {
                            this.$Message.error(res.data.data);
                            this.verifyModal=false;
                            this.$refs.slideblock.reset();
                        }
                    }).catch(err=>{
                    console.log(err)
                })
            },
            onSuccess(){
              this.verfiySuccess();
            },
            onFail(){
                this.$Message.error("");
            },
            onAgain() {
                this.$Message.warning("");
                this.$refs.slideblock.reset();
            },
            login(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.verifyModal=true;
                    } else {
                        this.$Message.error('!');
                    }
                })
            },
            findPass () {
                setTimeout(() => {
                        this.$refs['findPassForm'].validate((valid) => {
                            if (valid) {
                                let findPassUser=this.$qs.stringify(this.findPassForm);
                                this.axios.post('/customer/findPassword',findPassUser)
                                    .then(res=>{
                                        if(res.data.data){
                                            this.loading=false;
                                            this.findPassModal = false;
                                            this.$Message.success('!');
                                            this.$refs['findPassForm'].resetFields();
                                        }else{
                                            this.$Message.error('!');
                                            this.loading=false
                                        }
                                    }).catch(err=>{
                                    console.log(err)
                                    this.loading=false
                                });
                            }else {
                                this.$Message.error('!');
                                this.loading=false
                            }
                        });
                }, 1500);
            },
            addUserSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        let user=this.$qs.stringify(this.addUserForm);
                      this.axios.post('/customer/addCustomer',user)
                          .then(res=>{
                              if(res.data.data.result){
                                  this.$refs[name].resetFields();
                                  this.addUserDrawer=false;
                                  this.$Message.success('!');
                              }else {
                                  this.$Message.error('!');
                              }
                          }).catch(err=>{
                          console.log(err)
                      })
                    } else {
                        this.$Message.error('!');
                    }
                })
            },
        },
        created:function () {
            let that = this;
            document.onkeydown = function(e){
                let key = window.event.keyCode;
                if(key === 13 || key === 100){
                    that.login('loginForm');

                }

            }
        },
    }
</script>

<style lang="less" scoped>
.ivu-form-item{
margin-top: 20px;
}
    .forgetPass{
        margin-left: 20px;
        cursor:pointer;
        font-size: 12px;
        color: darkgray;
    }
    .addUserDrawerFooter{
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        border-top: 1px solid #e8e8e8;
        padding: 10px 16px;
        text-align: right;
        background: #fff;

    }
</style>