<template>
    <div>
        <Card style="margin: 4%">
            <Row>
                <Col :xs="{ span: 24, offset: 0 }" :sm="{ span: 20, offset: 0 }"  :md="{ span: 18, offset: 1 }" :lg="{ span: 13, offset: 1 }">
            <Card dis-hover :bordered="false">
            <h2 style="text-align: center"></h2>
            <Form ref="upUserForm" :model="upUserForm" :rules="rules" :label-width="80" style="margin-top: 40px">
                <FormItem label="" prop="username">
                    <Input v-model="upUserForm.username" placeholder=""></Input>
                </FormItem>
                <FormItem label="" prop="realname">
                    <Input v-model="upUserForm.realname" placeholder=""></Input>
                </FormItem>
                <FormItem label="" prop="number">
                    <Input v-model="upUserForm.number" placeholder=""></Input>
                </FormItem>
                <FormItem label="" prop="email">
                    <Input v-model="upUserForm.email" placeholder=""></Input>
                </FormItem>
                <FormItem label="" prop="autograph">
                    <Input  maxlength="40" show-word-limit v-model="upUserForm.autograph" placeholder=""></Input>
                </FormItem>
                <FormItem>
                    <Button type="primary" @click="addUserSubmit('upUserForm')"></Button>
                    <Button style="margin-left: 20px" @click="upPassModal=true"></Button>
                </FormItem>
            </Form>
                <Modal v-model="upPassModal"
                       title="">
                    <Form ref="upPassForm" :model="upPassForm" :rules="passRules" :label-width="80" style="margin-top: 20px">
                        <FormItem label="" prop="oldPass">
                            <Input type="password" v-model="upPassForm.oldPass" placeholder="" password></Input>
                        </FormItem>
                        <FormItem label="" prop="password">
                            <Input type="password" v-model="upPassForm.password" placeholder="" password></Input>
                        </FormItem>
                        <FormItem label="" prop="checkPass">
                            <Input type="password" v-model="upPassForm.checkPass" placeholder="" password></Input>
                        </FormItem>
                    </Form>
                    <div slot="footer">
                        <Button type="primary" @click="upPass"></Button>
                        <Button  @click="upPassModal=false"></Button>
                    </div>
                </Modal>
            </Card>
                </Col>
                <Col  :xs="24" :sm="16" :md="12" :lg="8">
                    <Avatar :src="user.userimg" size="150" style="margin-left:40%;margin-top: 10%"></Avatar>
                    <div>
                        <Upload action="http://localhost:8088/api/customer/uploadUserImg"
                                :headers="token" :data="user"
                                :on-success="handleSuccess"
                                :format="['jpg','jpeg','png']"
                                :max-size="2048"
                                :on-format-error="handleFormatError"
                                :on-exceeded-size="handleMaxSize"
                                :on-error="handleError">
                            <Button icon="ios-cloud-upload-outline" style="margin:50% 210%"></Button>
                        </Upload>
                    </div>
                </Col>
            </Row>
        </Card>
    </div>
</template>

<script>
    export default {
        name: "UpUserInfo",
        data(){
            var validateRealname = (rule, value, callback) => {

                if (Number.isFinite(value)) {
                    callback(new Error('!'));
                }
                else if(value.length<2||value.length>20){
                    callback(new Error('!'));
                }
                else {
                    callback();
                }
            };
            var validateNumber = (rule, value, callback) => {
                //
                if (!(/^1[3456789]\d{9}$/.test(value))){
                    callback(new Error('!'));
                }
                else {
                    callback();
                }
            };
            var validateOldPass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    let user=JSON.parse(localStorage.getItem('loginUser'));
                    let password=user.password;
                    if (this.upPassForm.checkPass !== '') {
                        this.$refs.upPassForm.validateField('checkPass');
                    }if(password!==value){
                        callback(new Error(''));
                    }
                    callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.upPassForm.checkPass !== '') {
                        this.$refs.upPassForm.validateField('checkPass');
                    }
                    if(value.length<6||value.length>14){
                        callback(new Error('6-14'));
                    }
                    if(value===this.upPassForm.oldPass){
                        callback(new Error(''));
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.upPassForm.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return{
                user:JSON.parse(localStorage.getItem('loginUser')),
                upUserForm:{
                    userid:JSON.parse(localStorage.getItem('loginUser')).userid,
                    rowid:JSON.parse(localStorage.getItem('loginUser')).rowid,
                    username:JSON.parse(localStorage.getItem('loginUser')).username,
                    realname:JSON.parse(localStorage.getItem('loginUser')).realname,
                    number:JSON.parse(localStorage.getItem('loginUser')).number,
                    email:JSON.parse(localStorage.getItem('loginUser')).email,
                    autograph:JSON.parse(localStorage.getItem('loginUser')).autograph,
                    password:JSON.parse(localStorage.getItem('loginUser')).password
                },
                token:{token:localStorage.getItem('token')},
                rules: {
                    username: [
                        { required: true, message: '', trigger: 'blur' },
                        { min:3,max:10, message: '3-10', trigger: 'blur' },
                    ],
                    realname:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateRealname, trigger: 'blur' }
                    ],
                    number:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateNumber, trigger:'blur' }
                    ],
                    email:[
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'email', message: '', trigger: 'blur'}
                    ]
                },
                upPassModal:false,
                upPassForm:{
                    oldPass:'',
                    password:'',
                    checkPass:''
                },
                passRules:{
                    oldPass: [
                        { validator: validateOldPass, trigger: 'blur' }
                    ],
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                },
            }
        },
        methods:{
            addUserSubmit (name) {
                this.$refs[name].validate((valid) => {
                    let user=this.$qs.stringify(this.upUserForm);
                    if (valid) {
                        this.axios.post('/user/updateUser',user)
                            .then(res=>{
                                console.log(res)
                                if(res.data.data.result){
                                    this.$Message.success('!');
                                    localStorage.setItem('loginUser',JSON.stringify(res.data.data.newUser))
                                }else {
                                    this.$Message.success('!')
                                }
                            }).catch(err=>{
                                console.log(err);
                        });
                    } else {
                        this.$Message.error('!');
                    }
                })
            },
            handleSuccess:function (response, file, fileList) {
                if(response.data.result){
                    this.$Message.success('');
                }else {
                    this.$Message.error('');
                }
                let newUser=JSON.stringify(response.data.newUser);
                localStorage.setItem('loginUser',newUser)
                window.location.reload()
            },
            handleFormatError (file) {
                this.$Message.warning('JPEG/JPG/PNG');
            },
            handleMaxSize (file) {
                this.$Message.warning('2m');
            },
            handleError(){
                this.$Message.warning('');
            },
            upPass:function () {
                this.$refs['upPassForm'].validate((valid) => {   //validatetruefalse
                    if (valid) {
                        let user = JSON.parse(localStorage.getItem('loginUser'));
                        user.password = this.upPassForm.password;
                        let newUser=this.$qs.stringify(user);
                        this.$axios.post('/user/updateUser',newUser,
                            {headers:{'token':localStorage.getItem('token')}})
                            .then(res=>{
                                if(res.data.data.result){
                                    this.$Message.success(",");
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    this.$router.push('/login');
                                }else {
                                    this.$Message.error('')
                                }
                            }).catch(err=>{
                           console.log(err)
                        });
                    }else {
                      this.$Message.error('!');
                        return false;
                    }
                });
            }
        }
    }
</script>

<style scoped>

</style>